import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  public consumerKey:string = "";
  public consumerSecret:string = "";
constructor(private http: HttpClient){}
  onSubmit():void{
	  console.log(this.consumerKey);
	  console.log(this.consumerSecret);
	  let rBody = {"consumerKey":this.consumerKey,"consumerSecret":this.consumerSecret}
	  this.http.post("http://localhost:8185/api/consumer/test",rBody).subscribe(data => {
	  console.log(data)
	  });
  }
}
